import React from 'react';
import {Fragment} from 'react';
import Navbar from './Navbar';
import Code from './Code';
import CustomInput from './CustomInput';
import Output from './Output';
import {Row, Col} from 'react-bootstrap';
import {displayError, load} from './FetchCalls';
import equal from 'fast-deep-equal'
import Button from "react-bootstrap/Button";
import Modal from "react-bootstrap/Modal";

class CodeEditor extends React.Component{
    constructor(){
        super();
        this.state = {
            language: "c++",
            data: "",
            startingLineNo: 0,
            length: 0,
            fileName: "scratch",
            searchResult: null,
            showError: false,
            errorMsg: "",
            needsOfflineEdit: false,
            inputs: ""
        };
        this.changeLan = this.changeLan.bind(this);
        this.writeFromFile = this.writeFromFile.bind(this);
        this.updateStartingLineNo = this.updateStartingLineNo.bind(this);
        this.updateData = this.updateData.bind(this);
        this.setData = this.setData.bind(this);
        this.scrollToRow = this.scrollToRow.bind(this);
        this.setSearchResult = this.setSearchResult.bind(this);
        this.showErrorMsg = this.showErrorMsg.bind(this);
        this.hideErrorMsg = this.hideErrorMsg.bind(this);
        this.setContent = this.setContent.bind(this);
        this.setContentLength = this.setContentLength.bind(this);
        this.forceEditCall = this.forceEditCall.bind(this);
        this.setInputs = this.setInputs.bind(this);
        this.setResult = this.setResult.bind(this);
        this.setFileName = this.setFileName.bind(this);
    }

    scrollToRow(row){
        let scrollElement = document.getSelection().focusNode.parentElement;
        let searchRow = row //- this.state.startingLineNo;
        console.log(searchRow);
        if((scrollElement.scrollTop)>row*25*window.devicePixelRatio || (scrollElement.scrollTop + scrollElement.scrollHeight) <= searchRow*25*window.devicePixelRatio){
            scrollElement.scrollTop = searchRow*25*window.devicePixelRatio;
        }
    }

    changeLan = evt =>{
        this.setState({language: evt});
        document.querySelector('.code-input').focus();
    }

    updateLanguage(page) {
        console.log(page);
        console.log('Startingline number = ' + page['startingLineNo']);
        var tokens = page['fileName'].split('.');
        var ext = tokens[tokens.length-1];
        const language = {
            'cpp' : 'C++',
            'py' : 'Python',
            'java' : 'Java',
            'c': 'C'
        };
        var lang = ext;
        if (ext in language){
            lang = language[ext];
        }
        this.setState({language:lang});
        this.setState({startingLineNo:parseInt(page['startingLineNo'])});
        this.setState({fileName:page['fileName']});
        console.log("done updating language");
    }

    async writeFromFile(file_info) {
        console.log('Starting writeFromFile');
        let page = await load(file_info, this.showErrorMsg);
        if(page.status != null && page.status !== 200) {
            return displayError(page, this.showErrorMsg());
        }
        this.updateLanguage(page);
        var code = page['lines'].join('\n');
        this.setState({data:code});
        console.log('done writeFromFile');
    }

    updateStartingLineNo(lineNo) {
        this.setState({startingLineNo:lineNo})
    }

    setSearchResult(searchResult) {
        this.setState({searchResult:searchResult});
    }

    setData(content, contentLength) {
        this.setState({data:content})
        this.setState({length:contentLength})
    }

    setContent(content) {
        this.setState({data:content})
        this.setState({needsOfflineEdit: true})
    }

    setContentLength(contentLength) {
        this.setState({length:contentLength})
        this.setState({needsOfflineEdit: true})
    }

    setFileName(filename) {
        this.setState({fileName:filename})
    }

    updateData(content, before, newContentLength) {
        var newContent = "";
        if (before) {
            newContent = content + '\n' + this.state.data
        } else {
            newContent = this.state.data + '\n' + content
        }
        this.setState({data:newContent})
        this.setState({length:this.state.length + newContentLength})
    }

    componentDidUpdate(prevProps){
        if(!equal(this.state, prevProps.state)){
            this.render();
        }
    }

    hideErrorMsg() {
        this.setState({showError: false});
    }

    showErrorMsg(errorMsg) {
        this.setState({showError: true});
        this.setState({errorMsg: errorMsg});
    }

    forceEditCall(force = true) {
        this.setState({needsOfflineEdit: force});
    }

    setInputs(inputs) {
        this.setState({inputs: inputs});
    }

    setResult(result) {
        this.setState({result: result});
    }

    render(){
        return(
            <Fragment>
                <Modal show={this.state.showError}>
                    <Modal.Header>
                        <Modal.Title>Error occured while calling the backend</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <div className={"error-body"}>
                        {this.state.errorMsg}
                        </div>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button variant="secondary" onClick={this.hideErrorMsg}>
                            Close
                        </Button>
                    </Modal.Footer>
                </Modal>

                <Navbar
                    language={this.state.language}
                    changeLan = {this.changeLan}
                    writeFromFile = {this.writeFromFile}
                    state={this.state}
                    setData={this.setData}
                    updateStartingLineNo={this.updateStartingLineNo}
                    setSearchResult={this.setSearchResult}
                    scrollToRow = {this.scrollToRow}
                    showErrorMsg = {this.showErrorMsg}
                    setResult={this.setResult}
                    setFileName={this.setFileName}
                />
                <Row style={{height:"93vh", margin:"0px",  background:"rgb(30,31,37)", overflowY:"hidden"}}>
                    <Col sm='6' style={{margin:"0px", padding:"0px"}}>
                        <div style={{height:"100%", width:"100%", padding:"7px 7px 7px 7px"}}>
                            <Code
                                state={this.state}
                                updateStartingLineNo={this.updateStartingLineNo}
                                updateData={this.updateData}
                                setData={this.setData}
                                setSearchResult={this.setSearchResult}
                                scrollToRow = {this.scrollToRow}
                                showErrorMsg = {this.showErrorMsg}
                                hideErrorMsg = {this.hideErrorMsg}
                                setContent = {this.setContent}
                                setContentLength = {this.setContentLength}
                                forceEditCall = {this.forceEditCall}
                            />
                        </div>
                    </Col>
                    <Col sm='6' style={{margin:"0px", padding:"0px"}}>
                        <Row style={{height:"46.5vh", margin:"0px"}}>
                            <div style={{height:"100%", width:"100%", padding:"0px 7px 0px 7px"}}>
                                <CustomInput
                                    state={this.state}
                                    setInputs={this.setInputs}
                                />
                            </div>
                        </Row>
                        <Row style={{height:"46.5vh", margin:"0px"}}>
                            <div style={{height:"100%", width:"100%", padding:"0px 7px 7px 7px"}}>
                                <Output
                                    state={this.state}
                                />
                            </div>
                        </Row>
                    </Col>
                </Row>
            </Fragment>
        );
    }
}

export default CodeEditor;
