import React from 'react';
import {Row, Col} from 'react-bootstrap';
import Highlight from './Highlight';
import equal from 'fast-deep-equal';
import {getCopyBuffer, setCopyBuffer, edit, jump, replace, save, redo, undo, getPrevLines, getNextLines, search, displayError} from './FetchCalls';
import Editor from 'react-simple-code-editor';
import { highlight, languages } from 'prismjs/components/prism-core';
import 'prismjs/components/prism-clike';
import 'prismjs/components/prism-javascript';
import 'prismjs/components/';
import '../node_modules/prismjs/components/prism-java';
import '../node_modules/prismjs/components/prism-c';
import '../node_modules/prismjs/components/prism-cpp';
import '../node_modules/prismjs/components/prism-python';
import './Code.css';

class Code extends React.Component{

    fetchNewLines = false;

    constructor(){
        super();
        this.handleKeyDown = this.handleKeyDown.bind(this);
        this.handleFindUpDownEsc = this.handleFindUpDownEsc.bind(this);
        this.jumpToSearchLine = this.jumpToSearchLine.bind(this);
        this.handleScroll = this.handleScroll.bind(this);
        this.handleMouseClick = this.handleMouseClick.bind(this);
        this.preventDefaultClipboardEvents = this.preventDefaultClipboardEvents.bind(this);
        this.isSameData = this.isSameData.bind(this);
        this.isSameDataLength = this.isSameDataLength.bind(this);
        this.languagesOffered = {
            'C': languages.c,
            'c': languages.c,
            'c++': languages.cpp,
            'C++': languages.cpp,
            'Java': languages.java,
            'java': languages.java,
            'Python': languages.python,
            'python': languages.python,
        };
    }

    getCaretLoc(data=document.querySelector(".code-input").value, selection=document.querySelector(".code-input").selectionEnd){
        var row = 0;
        var col = 0;
        data = data.split("\n");
        var prevLen = 0;
        while(row<data.length && (prevLen+data[row].length+1)<selection){
            prevLen=prevLen+data[row].length+1;
            row++;
        }
        col = selection-prevLen;
        return [row,col];
    }

    setCaretLoc(row, col, len=0){
        var textArea = document.querySelector('.code-input');
        var data = textArea.value;
        data = data.split("\n");
        var prevLen = 0;
        for (var i = 0; i < row; i++) {
            if (i < data.length) {
                prevLen = prevLen + data[i].length + 1;
            }
        }
        prevLen += col;
        textArea.selectionStart=prevLen;
        textArea.selectionEnd=prevLen+len;
    }

    async jumpToSearchLine(listOfCursors, lastIndex, self){
        var lineNo = listOfCursors[lastIndex]['lineNo'];
        let searchTermRow = lineNo;
        let searchLen = document.getElementById('find').value.length;
        var body;
        if(lineNo < self.props.state.startingLineNo){
            body = await jump(lineNo, self.props.state.fileName, this.getCaretLoc());
        }
        else if(lineNo>=(self.props.state.startingLineNo+self.props.state.data.split("\n").length)){
            body = await jump(lineNo, self.props.state.fileName, this.getCaretLoc());
        }
        else{
            body = await jump(this.props.state.startingLineNo, self.props.state.fileName, this.getCaretLoc());
        }
        if(body.status != null && body.status !== 200) {
            displayError(body, self.props.showErrorMsg);
        } else {
            var content = body['lines'].join('\n');
            await self.props.setData(content);
            await self.props.updateStartingLineNo(body['startingLineNo']);
            searchTermRow = lineNo - this.props.state.startingLineNo;
            this.setCaretLoc(searchTermRow, listOfCursors[lastIndex]['columnNo'], searchLen);
            self.props.scrollToRow(searchTermRow);
        }
    }

    async handleFindUpDownEsc(evt){
        let self = this;
        if(evt.key==='Escape'){
            document.getElementById('find').value='';
            self.props.setSearchResult(null);
        }
        else{
            let lastSearch = self.props.state.searchResult;
            let listOfCursors = lastSearch['listOfCursors'];

            var incr = {
                'ArrowUp' : -1,
                'ArrowDown' : +1
            }

            if (evt.key in incr && lastSearch!==null && listOfCursors.length > 0) {
                // console.log("========Key up/down received============");
                // console.log(lastSearch)
                var lastIndex = lastSearch['presentIndex'];

                lastIndex = (lastIndex + incr[evt.key] + listOfCursors.length) % listOfCursors.length;
                lastSearch['presentIndex'] = lastIndex;
                self.props.setSearchResult(lastSearch);
                self.jumpToSearchLine(listOfCursors, lastIndex, self);
            }
        }
        evt.preventDefault();
    }

    async paste(content) {
        var position = document.querySelector('.code-input').selectionStart;
        let length = this.props.state.length;
        var buffer = await getCopyBuffer(this.props.showErrorMsg);
        var p1 = content.substr(0, position)
        var p2 = content.substr(position, content.length)
        var newContent = p1 + buffer + p2
        await this.props.setContent(newContent, length)
        document.querySelector('.code-input').selectionStart = position;
        document.querySelector('.code-input').selectionEnd = position;
        return newContent;
    }

    async cut(selectionStart, selectionEnd){
        let content = document.querySelector('.code-input').value;
        let data = await content.slice(0,selectionStart);
        data += await content.slice(selectionEnd, content.length);
        this.props.setContent(data, data.split("\n").length);
        document.querySelector('.code-input').selectionStart = selectionStart;
        document.querySelector('.code-input').selectionEnd = selectionStart;
    }

    async handleMouseClick(evt) {
        this.fetchNewLines = true;
    }

    preventDefaultClipboardEvents(e){
        e.preventDefault();
        e.nativeEvent.stopImmediatePropagation();
    }

    async handleKeyDown(evt) {
        let textArea = document.querySelector(".code-input");
        let data = textArea.value.split("\n");
        let [startRow,startCol] = this.getCaretLoc(textArea.value, textArea.selectionStart);
        let [endRow, endCol] = this.getCaretLoc(textArea.value, textArea.selectionEnd);
        var request_body = {
            pageId: 1,
            data : data,
            startRow : startRow,
            startCol: startCol,
            endRow: endRow,
            endCol: endCol
        }

        console.log(evt.key);
        if(evt.key < 255) {
            this.props.forceEditCall();
        }

        if(this.props.state.searchResult!==null){
            if(evt.key==='ArrowUp' || evt.key==='ArrowDown' || evt.key==='Escape'){
                this.handleFindUpDownEsc(evt);
            }
        } else if(evt.key==='ArrowUp' || evt.key==='ArrowDown') {
            this.fetchNewLines = true;
        }

        if(evt.ctrlKey){
            switch(evt.key){
                //FIXME: get host details from config file
                case 'c':
                    var data_copied = document.getSelection().toLocaleString();
                    setCopyBuffer(data_copied, this.props.showErrorMsg);
                    evt.preventDefault();
                    break;
                case 'f':
                    document.getElementById('find').focus();
                    evt.preventDefault();
                    break;
                case 'j':
                    document.getElementById('jump').focus();
                    evt.preventDefault();
                    break;
                case 'r':
                    replace(request_body, this.props.showErrorMsg);
                    evt.preventDefault();
                    break;
                case 's':
                    save(request_body, this.props.showErrorMsg);
                    evt.preventDefault();
                    break;
                // case 'd':
                //     var presentContent = this.props.state.data;
                //     var lineNo = this.props.state.startingLineNo + presentContent.split('\n').length;
                //     var body = await getNextLines(lineNo, this.props.state.fileName);
                //     this.props.updateData(body['lines'].join('\n'), false, body['lines'].length);
                //     evt.preventDefault();
                //     break;
                // case 'u':
                //     body = await getPrevLines(this.props.state.startingLineNo, this.props.state.fileName);
                //     this.props.updateStartingLineNo(body['startingLineNo']);
                //     this.props.updateData(body['lines'].join('\n'), true, body['lines'].length);
                //     evt.preventDefault();
                //     break;
                case 'v':
                    let newData = await this.paste(this.props.state.data);
                    await this.editBackEnd(this.getCaretLoc(), newData);
                    evt.preventDefault();
                    break;
                case 'x':
                    var data_copied = document.getSelection().toLocaleString();
                    let selectionStart = document.querySelector('.code-input').selectionStart;
                    let selectionEnd = document.querySelector('.code-input').selectionEnd;
                    setCopyBuffer(data_copied, this.props.showErrorMsg);
                    await this.cut(selectionStart, selectionEnd);
                    await this.editBackEnd(this.getCaretLoc());
                    evt.preventDefault();
                    break;
                case 'y':
                    redo(request_body, this.props.showErrorMsg);
                    evt.preventDefault();
                    break;
                case 'z':
                    var body = await undo(this.props.state.fileName);
                    if(body.status != null && body.status !== 200) {
                        return displayError(body, this.props.showErrorMsg);
                    } else {
                        var content = body['lines'].join('\n');
                        this.props.setData(content, body['lines'].length);
                        this.props.updateStartingLineNo(body['startingLineNo']);
                    }
                    evt.preventDefault();
                    break;
                default :
            }
        }
    }

    async editBackEnd(caret, newData = document.querySelector('.code-input').value) {
        if (caret === undefined)
            return;

        var cursor = {
            'lineNo' : caret[0],
            'columnNo': caret[1]
        }
        let data = document.querySelector('.code-input').value.split("\n");
        let startingLineNo = this.props.state.startingLineNo;
        var req_body = {
            'startingLineNo' : this.props.state.startingLineNo,
            'endingLineNo' : this.props.state.startingLineNo + this.props.state.length,
            'newContent' : data,
            'fileName' : this.props.state.fileName,
            'cursorAt' : cursor
        }
        await edit(req_body, this.props.showErrorMsg);
        this.props.setContentLength(startingLineNo + data.length);
        this.props.forceEditCall(false);
    }

    handleScroll(evt){
        document.querySelector('.code-output').scrollLeft = document.querySelector('.code-input').scrollLeft;
    }

    isSameData(self){;
        return self.props.state.data.trim() === (document.querySelector('.code-input').value).trim();
    }

    isSameDataLength(self){
        return self.props.state.data.split('\n').length == self.props.state.length;
    }

    componentDidMount() {
        // Highlight.init('.code-input', '.code-output', '.language');
        let self = this;
        setInterval(async function(){
            let data = document.querySelector(".code-input").value;
            data = data.split("\n");
            let numberOfLines = data.length;
            document.getElementById('lineNo').innerHTML="";
            for(let i=self.props.state.startingLineNo;i<self.props.state.startingLineNo+numberOfLines;i++){
                document.getElementById("lineNo").innerHTML += "<div id='row" + (i) + "'>"+(i)+"</div>";
            }
            var [row,col] = self.getCaretLoc();
            if(self.props.state.data.length>0 && self.fetchNewLines){
                if(self.props.state.needsOfflineEdit){
                    // console.log(self.props.state.data);
                    // console.log(document.querySelector('.code-input').value)
                    self.editBackEnd(self.getCaretLoc());
                }
                self.fetchNewLines = false;
                if(Math.abs(row)<5 && self.props.state.startingLineNo!==0){
                    let body = await getPrevLines(self.props.state.startingLineNo, self.props.state.fileName, self.getCaretLoc());
                    if(body.status != null && body.status !== 200) {
                        displayError(body, self.props.showErrorMsg);
                    } else if(body['lines'].length !== 0){
                        self.props.updateStartingLineNo(body['startingLineNo']);
                        self.props.updateData(body['lines'].join('\n'), true, body['lines'].length);
                    }
                }
                else if(Math.abs(self.props.state.data.split("\n").length - row)<5){
                    let presentContent = self.props.state.data;
                    let lineNo = self.props.state.startingLineNo + presentContent.split('\n').length;
                    let body = await getNextLines(lineNo, self.props.state.fileName, self.getCaretLoc());
                    // console.log(body);
                    if(body.status != null && body.status !== 200) {
                        displayError(body, self.props.showErrorMsg);
                    } else if(body['lines'].length !== 0){
                        self.props.updateData(body['lines'].join('\n'), false, body['lines'].length);
                        self.setCaretLoc(body['cursorAt']['lineNo'], body['cursorAt']['columnNo']);
                    }
                }
            }
            document.getElementById('rowNumber').innerHTML = "Row: "+ (self.props.state.startingLineNo + row);
            document.getElementById('colNumber').innerHTML = "Col: "+col;
        }, 500)

        setInterval(function() {
            if(self.props.state.needsOfflineEdit){
                // console.log(self.props.state.data);
                // console.log(document.querySelector('.code-input').value)
                self.editBackEnd(self.getCaretLoc());
            }
        }, 1000);

    }

    componentDidUpdate(prevProps) {
        if(!equal(this.props.state, prevProps.state)){
          this.render();
        }
        // Highlight.init('.code-input', '.code-output', '.language');
    }

    render = () => {
        return (
            <div style={{height:"100%", width:"100%"}}>
                <Row style={{height:"calc(100vh - 80px)", margin:"0px",background:"#000", overflow:"auto"}}>
                    <Col sm='1'id="row_num" style={{background:"rgba(30,31,37,0.5)", padding:"0px", margin:"0px"}}>
                        <div
                            id="lineNo"
                            style={{width:"100%", textAlign:"center", color:"#f4f4f4", fontSize:"1.0rem"}}
                        >
                            <div>1</div>
                        </div>
                    </Col>
                    <Col sm='11' style={{padding:"0px", margin:"0px"}} spellCheck='false'>

                        <Editor
                            value={this.props.state.data}
                            onValueChange={code => this.props.setContent( code )}
                            highlight={code => highlight(code, this.languagesOffered[this.props.state.language])}
                            padding={2}
                            onKeyDown={this.handleKeyDown}
                            onCut = {this.preventDefaultClipboardEvents}
                            onCopy = {this.preventDefaultClipboardEvents}
                            onPaste = {this.preventDefaultClipboardEvents}
                            onClick={this.handleMouseClick}
                            textareaClassName={"code-input"}
                            preClassName={"code-output"}
                            autoFocus={true}
                        />


                        {/*<textarea*/}
                        {/*    className='code-input'*/}
                        {/*    onKeyDown={this.handleKeyDown}*/}
                        {/*    onCut = {this.preventDefaultClipboardEvents}*/}
                        {/*    onCopy = {this.preventDefaultClipboardEvents}*/}
                        {/*    onPaste = {this.preventDefaultClipboardEvents}*/}
                        {/*    onClick={this.handleMouseClick}*/}
                        {/*    defaultValue = {this.props.state.data}*/}
                        {/*    onScroll = {this.handleScroll}*/}
                        {/*>*/}
                        {/*</textarea>*/}
                        {/*<pre className="code-output">*/}
                        {/*    <code*/}
                        {/*        className={"language-"+this.props.state.language}*/}
                        {/*    >*/}
                        {/*    </code>*/}
                        {/*</pre>*/}
                    </Col>
                </Row>
                <div style={{color:"#f4f4f4"}}>
                    <span id="responseTime">Response Time: 0ms</span>
                    <div style={{float:"right"}}>
                        <span id='rowNumber'>Row: 0</span>
                        <span>    </span>
                        <span id='colNumber'>Col: 0</span>
                    </div>
                </div>
            </div>
        );
    };
}
export default Code;
