import React from 'react';
import {Nav} from 'react-bootstrap';

class Output extends React.Component{
    render() {
        return(
            <div style={{height:"100%", width:"100%",background:"#000", margin:"0px"}}>
                <Nav style={{background:"rgb(30,31,38)", height:"10%", color:"#f4f4f4"}}>
                    <Nav.Item>
                        Output
                    </Nav.Item>
                </Nav>
                <textarea readOnly
                          style={{height:"90%", width:"100%",background:"#000", border:"0px", color:"#a0a0a0"}}
                          value={this.props.state.result}
                >
            </textarea>
            </div>
        );
    }
}

export default Output;
