import React from 'react';
import {Nav} from 'react-bootstrap';

class CustomInput extends React.Component{

    render() {
        return(
            <div style={{height:"100%", width:"100%",background:"#000", margin:"0px"}}>
                <Nav style={{background:"rgb(30,31,38)", height:"10%", color:"#f4f4f4"}}>
                    <Nav.Item>
                        Custom Input
                    </Nav.Item>
                </Nav>
                <textarea
                    style={{fontSize:"2vh",height:"90%", width:"100%",background:"#000", border:"0px", color:"#a0a0a0"}}
                    onChange={event => this.props.setInputs(event.target.value)}
                >
            </textarea>
            </div>
        );
    }
}

export default CustomInput;
