function server_url() {
    var url = window.location.href;
    console.log(url);
    return url
}

function packRequest(request_body){
    var req_body = {
        method: 'POST',
        mode:'cors',
        headers: {
            'Content-Type': 'application/json'
        },
        body: JSON.stringify(request_body)
    }
    return req_body;
}

export async function setCopyBuffer(data_copied, showErrorMsg) {
    console.log("Data copied = " + data_copied)
    var request_body = {
        'lines' : data_copied.split('\n')
    }

    let startTime = Date.now();
    var response = await fetch(server_url() + "/qcharm/copy_buffer", packRequest(request_body));
    var body = await response;
    let responseTime = Date.now() - startTime;
    document.getElementById('responseTime').innerHTML = "Response Time: " + responseTime + "ms";
    console.log(body);
    if(body.status != null && body.status !== 200) {
        return displayError(body, showErrorMsg);
    }
}

export async function displayError(body, showErrorMsg) {
    if (body.status === 400) {
    } else {
        showErrorMsg(body.trace);
    }
    return "";
}

export async function getCopyBuffer(showErrorMsg) {
    let startTime = Date.now();
    var response = await fetch(server_url() + "/qcharm/copy_buffer");
    var body = await response.json();
    let responseTime = Date.now() - startTime;
    document.getElementById('responseTime').innerHTML = "Response Time: " + responseTime + "ms";

    if(body.status != null && body.status !== 200) {
        return displayError(body, showErrorMsg);
    }
    return body['lines'].join('\n');
}

export async function search(startingLineNo, searchPattern, fileName){
    // Sends the find term
    // Expects a list of positions of the findterm
    var request_body = {
        'startingLineNo' : startingLineNo,
        'pattern' : searchPattern,
        'fileName' : fileName
    };
    let startTime = Date.now();
    var response = await fetch(server_url() + "/qcharm/search", packRequest(request_body));
    var body = await response.json();
    let responseTime = Date.now() - startTime;
    document.getElementById('responseTime').innerHTML = "Response Time: " + responseTime + "ms";
    return body;
}

export async function jump(lineNo, fileName, caret) {
    // Sends the line number to jump to
    // Expects the page corresponding to the line number. !!RERENDERING REQUIRED
    var request_body = {
        'startingLineNo' : lineNo,
        'fileName' : fileName,
        'numberOfLines': 50, // FIXME: number of lines should be args
        'cursorAt': {
            'lineNo' : caret[0],
            'columnNo' : caret[1]
        }
    }

    let startTime = Date.now();
    let response = await fetch(server_url() + "/qcharm/jump", packRequest(request_body));
    var body = await response.json();
    let responseTime = Date.now() - startTime;
    document.getElementById('responseTime').innerHTML = "Response Time: " + responseTime + "ms";

    return body
}

export async function getPrevLines(lineNo, fileName, caret) {
    // FIXME: multiple load_file
    var request_body = {
        'startingLineNo' : lineNo,
        'fileName' : fileName,
        'numberOfLines': 50, // FIXME: number of lines should be args
        'cursorAt': {
            'lineNo' : caret[0] -1,
            'columnNo' : caret[1]
        }
    }
    let startTime = Date.now();
    let response = await fetch(server_url() + "/qcharm/get_prev_lines", packRequest(request_body));
    let endTime = Date.now();
    let responseTime = endTime-startTime;
    document.getElementById('responseTime').innerHTML = "Response Time: " + responseTime + "ms";
    //NOTE: reponse is a promise and it serves only once
    // hence two response.json() call will given some error
    var body = await response.json();
    return body;
}

