export async function undo(fileName) {
    // Sends nothing
    // Expects the page after the undo operation. !!RERENDERING REQUIRED
    var request_body = {
        'fileName' : fileName
    }
    let startTime = Date.now();
    let response = await fetch(server_url() + "/qcharm/undo", packRequest(request_body))
    var body = await response.json()
    let responseTime = Date.now() - startTime;
    document.getElementById('responseTime').innerHTML = "Response Time: " + responseTime + "ms";
    console.log(body);

    return body
}

export async function execute(filename, inputs) {
    // Sends nothing
    // Expects the page after the undo operation. !!RERENDERING REQUIRED
    var request_body = {
        'fileName' : filename,
        'inputs': inputs
    }
    let startTime = Date.now();
    let response = await fetch(server_url() + "/qcharm/run_file", packRequest(request_body))
    var body = await response.json()
    let responseTime = Date.now() - startTime;
    document.getElementById('responseTime').innerHTML = "Response Time: " + responseTime + "ms";
    console.log(body);

    return body.output;
}

